/********* Fonts******/

@font-face {
	src: 'fonts/Lato-Regular.ttf';
	font-family: 'Lato'; }


$color-orange: #f9ba2e;
$color: #fff;
/********* Basic Page Needs******/

body {
	font-family: 'Lato'; }
h1 {
	text-transform: uppercase;
	font-size: 48px;
	line-height: 66px;
	font-weight: 400;
	color: $color;
	text-align: center; }
a {
	transition: all .4s;
	color: $color; }
ul {
	list-style: none; }
.wrapper {
	max-width: 1200px;
	margin: 0 auto; }
.flex {
	display: flex;
	padding: 0 15px; }
.bold {
	font-weight: bold; }
.orange {
	color: #f9ba2e; }
.text-center {
	text-align: center; }
.text-left {
	text-align: left; }
.padder {
	padding: 4rem 0; }
.overlay {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(249,186,46, .3);
		width: 100%;
		min-height: inherit;
		filter: brightness(60%);
		z-index: -1; }
.btn {
	padding: 0 20px;
	background: #f9ba2e;
	display: inline-block;
	font-size: 18px;
	letter-spacing: 1px;
	margin: 2.5rem 0;
	transition: all .4s;
	position: relative;
	overflow: hidden;
	a {
		align-items: center;
		min-height: 50px;
		color: #000; }
	&:hover {
		background: rgba(249,186,46,.5); }
	&:hover a {
		color: $color; }
	&:hover .top-line {
		transform: translateY(-20px); } }
.top-line {
		width: 100%;
		height: 25px;
		background: $color;
		position: absolute;
		top: 0;
		left: 0;
		transform: translateY(-30px);
		transition: all .4s; }

/********* Header-styles *********/

.header {
	background: url('../img/bg-image-1.jpg') no-repeat center center;
	background-size: cover;
	width: 100%;
	min-height: 1200px;
	&-logo {
		img {
			max-width: 200px;
			height: auto; } }
	&-caption {
		color: #fff;
		h1 {
			margin: 5rem 0 2rem; }
		p {
			margin: auto;
			line-height: 20px;
			font-size: 16px;
			max-width: 800px; }
		.count-list {
			justify-content: center;
			flex-wrap: wrap;
			margin: 5rem 0;
			&__item {
				width: 17%;
				padding: 0 10px; }
			h2 {
				font-size: 72px;
				font-weight: bold; }
			span {
				display: inline-block;
				width: 50%;
				font-size: 20px;
				line-height: 24px; } } }
	.blocks {
		&-list {
			flex-wrap: wrap;
			justify-content: space-between;
			&__item {
				background: rgba(249,186,46,.5);
				width: 31%;
				min-height: 160px;
				transition: all .4s;
				position: relative;
				overflow: hidden;
				&:hover {
					background: $color-orange; }
				&:hover .top-line {
					transform: translateY(0); } }
			&__link {
				line-height: 26px;
				font-size: 22px;
				height: 100%;
				color: #fff;
				align-items: center;
				text-transform: uppercase; } }
		&-img {
			margin-right: 15px;
			font-size: 48px;
			align-items: center; } } }

/********* Tips-Section-Styles *********/
.tips {
	background: url('../img/bg-image-2.jpg') no-repeat center center;
	background-size: cover;
	width: 100%;
	min-height: 810px;
	&-inner {
		flex-direction: column;
		justify-content: space-between;
		min-height: 810px; }
	h1 {
		max-width: 810px;
		margin: 0 auto;
		line-height: 1.416666666em;
		letter-spacing: .1em; }
	&-list {
		justify-content: space-between;
		flex-wrap: wrap;
		margin: 3rem 0;
		&__item {
			text-align: center;
			color: $color;
			width: 30%;
			padding: 0 15px;
			h4 {
				text-transform: uppercase;
				font-size: 20px; }
			p {
				line-height: 1.2rem;
				margin: 1.5rem 0;
				font-size: 16px; }
			a {
				color: $color;
				font-size: 18px;
				transition: all .4s;
				&:hover {
					color: $color-orange; } }
			.line {
				width: 30px;
				height: 2px;
				margin: auto;
				background: $color;
				margin-bottom: 1.5rem; } } }
	&-links {
		color: $color-orange;
		text-transform: uppercase;
		line-height: 30px;
		a {
			color: $color-orange;
			margin: 0 10px;
			transition: all .4s;
			&:hover {
				color: $color; } }
		.btn {
			a {
				text-transform: none;
				color: #000; } } }
	&-caption {
		max-width: 800px;
		margin: auto;
		text-align: center;
		font-size: 20px; } }

/********* Team-Section-Styles *********/
.team {
	&-list {
		padding: 0;
		flex-wrap: wrap;
		justify-content: flex-start;
		&__item {
			width: 33.3%;
			overflow: hidden;
			height: 21vw;
			position: relative;
			background: rgba(249,186,46, .3);
			&:hover img {
				transform: scale(1.1); }
			&:hover:after {
				opacity: 1; }
			&:hover a {
				opacity: 1; }
			&:hover .top-line {
				transform: translateY(0); }
			img {
				width: 100%;
				transition: all .4s; }
			&:after {
				content: '';
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				background: rgba(249,186,46, .3);
				opacity: 0;
				transition: all .4s; }
			a {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 100%;
				padding: 0 25px;
				display: inline-block;
				opacity: 0;
				transition: all .4s;
				font-size: 48px;
				text-align: center;
				line-height: 60px;
				z-index: 999;
				&:hover {
					color: rgba(0,0,0,.5); } } } } }




/********* Benefit-Section-Styles *********/

section.padder {
	padding: 6rem 0; }
.benefit {
	background: $color-orange;
	color: $color;
	&-caption {
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-between; }
	.main-text {
		width:: 34% {}
		h2 {
			font-size: 48px; } }
	.middle-text {
		width: 33%;
		p {
			font-weight: 900;
			font-size: 20px;
			line-height: 30px;
			text-transform: uppercase; } }
	&-link {
		font-size: 18px;
		margin-right: 15px;
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: -60px;
			width: 3px;
			height: 25px;
			background: $color;
			vertical-align: middle; }
		&:hover a {
			color: #000; } } }

/********* Testimonial-Section-Styles *********/
.testimonial {
	background: url('../img/bg-image-4.jpg') no-repeat center center;
	background-size: cover;
	min-height: 810px;
	width: 100%;
	&-slider {
		margin-top: 30px;
		justify-content: center;
		flex-wrap: wrap;
		&__item {
			position: relative;
			overflow: hidden;
			img {
				transition: all .4s; }
			&:after {
					content: '';
					background: rgba(249,186,46, .3);
					position: absolute;
					top: 0;
					bottom: 0;
					width: 100%;
					opacity: 0;
					filter: brightness(50%);
					transition: all .4s;
					z-index: 1; }
			&:hover:after {
				opacity: 1; }
			&:hover img {
				transform: scale(1.06); }
			&:hover .top-line {
				transform: translateY(0);
				z-index: 999; }
			&:hover .testimonial-caption {
				opacity: 1; } }
		.top-line {
			z-index: 999; } }
	&-caption {
		position: absolute;
		top: 50%;
		z-index: 999;
		min-width: 260px;
		left: 50%;
		color: $color;
		transform: translate(-50%, -50%);
		line-height: 18px;
		opacity: 0;
		transition: all .4s;
		span {
			font-size: 76px;
			display: block; }
		.quote-bottom {
			transform: rotateY(180deg) translateY(35px);
 }			// transform: translateY(45px)
		.client {
			transform: translateY(35px);
			span {
				font-size: 16px; } } } }



/********* Form-Section-Styles *********/
.form {
	background: url('../img/bg-image-5.jpg') no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
	width: 100%;
	position: relative;
	z-index: 1;
	.title {
		h1 {
			text-transform: none; } }
	&-section {
		margin: 30px 0;
		form {
			justify-content: space-between;
			flex-wrap: wrap;
			input {
				width: 48%;
				display: inline-block;
				border: 1px solid $color;
				background: transparent;
				padding: 15px 30px;
				color: $color;
				margin-bottom: 30px;
				&::placeholder {
					color: $color; } }
			textarea {
				resize: none;
				width: 100%;
				border: 1px solid $color;
				padding: 15px 30px;
				background: transparent;
				min-height: 200px;
				color: $color;
				&::placeholder {
					color: $color;
					font-size: 16px; } } }
		.btn {
			margin-bottom: 0; } } }

/********* Footer-Styles *********/
.footer {
	background: $color;
	&-list {
		flex-wrap: wrap;
		&__item {
			width: 26%;
			color: #57677a;
			font-size: 16px;
			padding: 0 15px;
			img {
				height: auto;
				max-width: 180px;
				margin-bottom: 15px; }
			p {
				font-size: 16px;
				line-height: 24px; }
			a {
				color: #57677a;
				&:hover {
					color: $color-orange; } }
			address {
				margin-top: 20px;
				line-height: 20px; } } }
	&-info {
		p + a {
			font-size: 20px; } }
	&-logo {
		a {
			font-size: 32px;
			margin: 0 10px; } } }
